<template>
    <div id="app">
        <app-header></app-header>
        <section class="paintingNew">
            <h6>发布画作</h6>
            <app-new></app-new>
            <app-restriction></app-restriction>
        </section>
        <!--<app-footer></app-footer>-->
    </div>
</template>

<!--组件导入-->
<script>
  import 'common/css/reset.css';
  import appHeader from 'components/Header/Header'
  import appFooter from 'components/Footer/Footer'
  import appList from 'components/Painting/List'
  import appRestriction from 'components/Painting/Restriction'
  import appNew from 'components/Painting/New'

  export default {
    name: 'app',
    components: {
      appHeader,
      appRestriction,
      appNew,
      appFooter,
    },
    data: () => {
      return {}
    }
  }
</script>

<style lang="scss" scoped>
    @import "../../common/css/reset.css";
    @import "../../common/css/define";

    .paintingNew {
        max-width: 1180px;
        margin: 0px auto ;

        >h6 {
            padding: 38px 0 20px 0;
            font-size: 22px;
            color: $black;
        }
    }
    #app {
        background-color: #fafafa;
    }
    .new,.restriction {
        background-color: #fff;
    }
</style>


